* {
    box-sizing: border-box;

}

body {
     margin: 0;   
     font-family: 'Poppins', sans-serif;

}

nav {
    height: 70px;
    display: flex;
    max-width: ;
    padding: 20px 35px;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);
}

.logo--nav {
    max-width: 100px;
}

section {
    padding: 20px;
}

.hero {
    /* background-image: url(/react/tuto2/src/Components/images/bg01.jpg); */
    display: flex ;
    flex-direction: column;
}

.hero--image {
    margin: 50px;
    max-width: 900px;
    align-self: center;
}

.hero--header {
    margin-bottom:  16px;
    font-size: 3rem;

}

.hero--text {
    margin-top: 0;
    font-size: large;
}


.carrousel {
    display: flex;
    justify-content: center;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;

}

.card {
    width: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: .5rem;
    font-size: 12px;
    background-color: rgb(255, 255, 255);
    border-radius: 9px;
    margin: 2rem;
    box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);

}

.card--star {
    width: 20px;
}

.card--image {
    width: 90%;
    height: 50%;
    align-self: center;
    border-radius: 9px 9px 9px 9px;
    padding-bottom: 20px;
    
}

.card--stats{
    display: flex;
    align-items: center;
    gap: .4rem;
    margin: 0px 20px;
    font-size: medium;
}

.description--card {
    font-size: large;
    overflow: hidden;
    text-overflow: ellipsis;

}

.gris {color: gray;}

.boldi {font-weight: 600;}

